* {
  margin: 0 auto;
  padding: 0 auto;
}

@keyframes background {
  50% {
    background: #314986;
  }
}

@keyframes color {
  50% {
    color: #314986;
  }
}

@keyframes border {
  50% {
    border-bottom: 4px solid #314986;
    border-top: 4px solid #314986;
  }
}

.downheader {
  font-weight: 200;
  font-size: 20px;
  padding-bottom: 30px;
}

.header-img {
  display: flex;
  justify-content: center;
  border-radius: 100%;
  border: 2px solid white;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 20px 25px -5px, rgba(0, 0, 0, 0.04) 0px 10px 10px -5px;
  width: 50%;
}

.links .material-icons {
  font-size: 2em;
}

.footer-container {
  padding: 1vh 1vh 1vh 1vh;
  font-size: 14px;
  font-weight: 300;
  line-height: 15px;
}

.footer-img {
  display: inline-block;
  width: 50px;
}
.scroll-arrow-section{
  padding-top: 25vh;
  text-align: center;
  color: #ffffff;
}

.scroll-arrow-section .material-icons{
  font-size: 60px;
}

.footer-message {
  color: #dadada;
}

.scroll-arrow-bottom{
  cursor: pointer;
}

.footer-text {
  margin-left: 10px;
  margin-top: 2px;
  display: inline-block;
  vertical-align: top;
  font-family: 'Raleway';
  font-style: normal;
  color: white;
}

.footer {
  margin-top: 0.4vh;
  margin-bottom: 4vh;
  font-size: 0.75em;
}

i1:hover {
  -webkit-transform: scale(1.2);
  -ms-transform: scale(1.2);
  transform: scale(1.2);
}

a:hover {
  animation: color 8s infinite alternate;
  color: #ffffff;
}

a {
  text-decoration: none;
  color: rgb(223, 223, 223);

}

.category {
  border-bottom: 4px solid #DC8585;
  border-top: 4px solid #DC8585;
  animation: border 8s infinite alternate;
  margin-bottom: 1px;
}


.category-header {
  font-size: 2em;
  padding: 6px 1vh 1vh 1vh;
  margin-bottom: 1.5vh;
}

.category-icons-container {
  margin-bottom: 2vh;
}

.category-icons .material-icons {
  font-size: 15px;
}

.category-icons {
  display: inline-block;
  width: 40%;
  padding: 0.7%;
  font-size: 1.35em;
}

.category-contents {
  margin-left: 1vw;
  margin-right: 3vw;
  padding-bottom: 2vh;
  font-weight: 200;
  font-size: 15px;
  color: white;
  font-family: 'Raleway';
  font-style: normal;
}

.main{
 poxui: 1;
}

.links {
  display: inline-block;
  margin-left: 30px;
  margin-right: 30px;
  padding-top: 50px;
  vertical-align: top;
  color: white;
}

.links:nth-child(4) i1 {
  margin-top: 0.9em;
}

.links:nth-child(5) i1 {
  margin-top: 1.5em;
}

.links:nth-child(6) i1 {
  margin-top: 1.5em;
}

.links:nth-child(7) i1 {
  margin-top: 0.9em;
}

.header {
  width: 100%;
  height: 1345px;
  display: flex;
  flex-direction: column;
}

.header-text {
  font-weight: 700;
  font-size: 32px;
  font-family: 'Raleway';
  font-style: normal;
  text-align: center;
  color: #FFFFFF;
}

.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  line-height: 1;
  letter-spacing: normal;
  text-transform: none;
  display: inline-block;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
}

.category-icon .material-icons {
  font-size: 15px;
}

.category-icon {
  display: inline-block;
  color: white;
}

.category-title {
  display: inline-block;
  margin-left: 3px;
  font-weight: 400;
  font-size: 0.7em;
  font-family: 'Raleway';
  font-style: normal;
  color: #FFFFFF;

}

.container {
  max-width: 1000px;
  padding-top: 15vh;
  margin-left: auto;
  margin-right: auto;
}

.home_page {
  max-width: 1000px;
  min-height: 100vh;
  -moz-user-select: none;
  -khtml-user-select: none;
  user-select: none;
  background: rgba(255, 255, 255, 0.17);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(16.2px);
  -webkit-backdrop-filter: blur(16.2px);
}




